import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Form,InputNumber, DatePicker,Tag,Switch,Checkbox} from 'antd';
import moment from 'moment';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,allStaffs,rooms,coaches} from '../../../entry/index/routes/IndexPage';
import Coach from '../../common/fields/coach';
import SportType from '../../common/fields/sportType';
class BizComponent extends Component {
  constructor(...args) {
  	super(...args);
  	emitter.on('studentRecordModal', (rec)=>{
        let formValues={roomId:rooms.length>0?rooms[0].id+'':undefined,consumeTimes:1,studyTime:moment(),type:''};
        this.setState({visible:true,formValues,rec});
      
    });
  	this.state={
      formValues:{},
      rec:{courses:[]},
      visible:false
  	};
  }

  handleOk=()=>{
    util.validateFields.call(this,values=>{
      let {rec} = this.state;
      util.ajaxJson(urlPrefix+'TrainOperation?opt=studentConsume', {...rec,...values}, res=>{
        if(res.success){
          message.success(`!`);
          this.handleCancel();
        }
      });
    });
  }

  handleCancel=()=>{
    emitter.emit(`refresh-student-table`);
    this.setState({visible:false});
  }

  onCoachChange=(v)=>{
    let coach=coaches.filter(it=>it.id==v)[0];
    if(!Coach)return;
    let {formValues}=this.state;
    let times=formValues.consumeTimes||1;
    formValues.consumeAmount=times*coach.unitPrice;
    this.setState({formValues});
  }

  render() {
    let {visible,formValues,rec} = this.state;
  	return (<Modal title={``} className="modal-form" visible={visible} width={1200} destroyOnClose onCancel={this.handleCancel} maskClosable={false} footer = {
        [<Button key="1" type="primary" size="large" onClick={this.handleOk}>
            
         </Button>,
         <Button key="2" type="ghost" size="large" onClick={this.handleCancel}>
            
         </Button>]
      }>
          <Form className="member-consume-modal">
            <Form.Item className="memberName-f" label="">
                {rec.name}
            </Form.Item>
            <Form.Item className="" label="">
                {rec.phone}
            </Form.Item>
            <SportType form={this} required/>
            <Form.Item className="" label="" required>
              <Select {...util.getField.call(this,'courseId',{onChange:v=>{
                let {formValues}=this.state;
                let {coachId}=rec.courses.filter(it=>it.id+''==v)[0];
                formValues.coachId=coachId+'';
                this.onCoachChange(formValues.coachId);
              }})}>
                {rec.courses.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{coaches.filter(c=>it.coachId==c.id)[0].name}({it.type})</Select.Option>;
                })}
              </Select>
            </Form.Item>
            <Form.Item className="" label="" required>
              <Coach form={this} required onChange={this.onCoachChange} showPrice/>
            </Form.Item>
            <Form.Item className="" label="" required>
              <InputNumber {...util.getField.call(this,'consumeTimes',{rules:[{required:true,message:''}]})} min={1} precision={0}/>
            </Form.Item>
            <Form.Item className="" label="()" required>
              <InputNumber {...util.getField.call(this,'consumeAmount',{rules:[{required:true,message:''}]})} min={1} precision={0}/>
            </Form.Item>
            <Form.Item className="" label="" required>
              <DatePicker {...util.getField.call(this,'studyTime',{rules:[{required:true,message:''}]})} min={1} precision={0}/>
            </Form.Item>
            <Form.Item className="" label="">
              <Select {...util.getField.bind(this)('roomId',{})}>
                {rooms.map(it=>{
                  return <Select.Option key={it.id} value={it.id+''}>{it.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            
          </Form>
      </Modal>);
  }
}

export default BizComponent;