import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon,Select,Upload, Popconfirm} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';
import {allRoles,isAdmin,urls,allStaffs,rooms, coaches} from '../../../entry/index/routes/IndexPage';
import AddStudentModal from './addStudentModal';
import StudentConsumeModal from './studentConsumeModal';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on('refresh-student-table',()=>{
      this.setState({expandedRowKeys:[]},()=>{
        emitter.emit(`queryStudentTable`, undefined, true);
      });
    });
    this.config = {
      title:'',
      key:'Student',
      dialog:{width:1060},
      delayQueryTable:1000,
      queryCond:{
        conditions:[[{key:'phone'},{key:'name'},{key:'operator'}]],
        extButtons:()=>{
          let extBtns=[<Button key="1" type="primary" size="small" className="search-button" onClick={()=>emitter.emit('addStudentModal')}><Icon type="plus" /></Button>,
          urls.map(it=>it.code).indexOf('downloadStudent')>-1&&<Popconfirm key="export" title="" onConfirm={()=>window.location.href=urlPrefix+'download/TrainOperation?opt=downloadStudent'}><Button size="small" className="search-button"><Icon type="download" /></Button></Popconfirm>,
          urls.map(it=>it.code).indexOf('uploadStudent')>-1&&<Upload key="file" name="file" className="search-button" action={urlPrefix+'upload/TrainOperation?opt=uploadStudent'} fileList={this.state.fileList} withCredentials onChange={res=>{
            if (res.file.status=='done') {
              if(res.file.response.success){
                message.success(res.file.response.data);
                emitter.emit(`queryStudentTable`);
              }else{
                message.error(`${res.file.response.errMsg}`, 20);
              }
            } else if(res.file.status=='error') {
              message.error(``);
            }
            let {fileList}=res;
            if(fileList&&fileList.length>1)fileList.splice(0,1);
            this.setState({fileList});
          }}>
            <Tooltip title="excel"><Button size="small" type="danger"><Icon type="upload" /></Button></Tooltip>
          </Upload>];
          return extBtns.filter(it=>!!it);
        },
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInForm:true,
          notInTable:true,
          label:'id'
        },{
          key:'name',
          width:'10%',
          rules:[{required: true, message: ''}],
          label:''
        },{
          key:'phone',
          width:'10%',
          rules:[{required: true, message: ''},{validator:v=>{
            return /^1[0-9]{10}$/.test(v);
          },message:'111'}],
          label:''
        },{
          key:'amount',
          width:'10%',
          rules:[{required: true, message: ''}],
          label:'()'
        },{
          key:'courses',
          width:'20%',
          notInForm:true,
          render:(v,r)=>{
            if(r.courses){
            return r.courses.map((it,idx)=><Tooltip key={idx+'c'} title={<span>{it.type}<br/>(){it.amount}</span>}><a className="course-item">{coaches.filter(c=>it.coachId==c.id)[0].name}({it.times})</a></Tooltip>);
            }
          },
          label:''
        },{
          key:'recordCount',
          width:'6%',
          label:''
        },{
          key:'operator',
          width:'6%',
          label:''
        },{
          key:'gmtCreate',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        },{
          key:'gmtModified',
          notInForm:true,
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        }
      ],
      operationAddon:rec=>{
        let useEmpty=false;
        if(rec.amount<=0){
          useEmpty=true;
        }
        return (<span><a disabled={useEmpty} onClick={()=>{
          emitter.emit('studentRecordModal',rec);
        }}></a></span>);
      },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:formData=>{
          formData.ext=JSON.stringify({staffs:formData.staffs});
          delete formData.staffs;
          return formData;
        }
      },
      table:{
        bordered:true,
        size:'small',
        expandedRowKeys:()=>this.state.expandedRowKeys,
        expandIcon:(pops)=>{
          let {expandedRowKeys}=this.state;
          let expanded=expandedRowKeys.indexOf(pops.record.id)>-1;
          if(!pops.record.recordCount)return;
          return <Icon type={expanded?"minus-square":"plus-square"} onClick={()=>{
            let {expandedRowKeys}=this.state;
            if(expanded){
              expandedRowKeys=[];
            }else{
              expandedRowKeys=[];
              expandedRowKeys.push(pops.record.id); 
            }
            this.setState({expandedRowKeys},this.refreshExpandRow);
          }}/>
        },
        expandedRowRender:(record, index, indent, expanded)=>{
          let {recDs}=this.state;
          return <Table className="student-rec-table" columns={[
          {key:'studyTime',dataIndex:'studyTime',title:'',render:v=>util.formatDate(v)},
          {key:'coachId',dataIndex:'coachId',title:'',render:v=>coaches.filter(it=>it.id==v)[0].name},
          {key:'consumeTimes',dataIndex:'consumeTimes',title:''},
          {key:'consumeAmount',dataIndex:'consumeAmount',title:'()'},
          {key:'snapAmount',dataIndex:'snapAmount',title:'()'},
          {key:'operator',dataIndex:'operator',title:''}]} rowKey={r=>r.id} dataSource={recDs.data} pagination={recDs.totalItem<=10?false:{total:recDs.totalItem}} size="small"/>;
        },
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.list.forEach(it=>{
          });
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'TrainOperation?opt=queryStudent'},
        // updateInterface:{func:urlPrefix+'TrainOperation?opt=updateStudent'},
        // deleteInterface:{func:urlPrefix+'TrainOperation?opt=deleteStudent',params:{id:'id'}}
      }
    };
    emitter.on('refresh-student-expand',this.refreshExpandRow);
    this.state={
      fileList:[],
      expandedRowKeys:[],
      recDs:{totalItem:0}
    };
  }

  refreshExpandRow=()=>{
    let {expandedRowKeys}=this.state;
    if(expandedRowKeys&&expandedRowKeys.length>0){
      util.ajaxJson(urlPrefix+'TrainOperation?opt=queryStudentRecord',{memberId:expandedRowKeys[0]},res=>{
        this.setState({recDs:res.data});
      });
    }
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
       <AddStudentModal />
       <StudentConsumeModal />
      </div>
    );
  }
}