import React,{Component} from 'react';
import {message,Input,Modal,Table,Button,Tooltip,Popover,Icon, DatePicker} from 'antd';
import './index.less';
import copy from 'copy-to-clipboard';
import {util,emitter} from '../../../common/common';
import BizComp from '../../../components/common';
import {urlPrefix,urlProductPrefix} from '../../../interfaces/interfaces';

import QueryCond from '../../../components/common/table/queryCond';

export default class BizComponent extends Component {
  constructor(...args) {
    super(...args);
    emitter.on(`menu-clicked-bill/queryBill`,()=>{
      emitter.emit('queryBillQueryTable');
    });
    this.config = {
      title:'',
      key:'BillQuery',
      dialog:{width:1060},
      delayQueryTable:1000,
      queryCond:{
        conditions:[[{key:'io'},{key:'domain'},{key:'cate'}],[{key:'startTime',type:'custom',label:'',getComponent:props=><DatePicker showTime {...props}></DatePicker>},{key:'endTime',label:'',type:'custom',getComponent:props=><DatePicker showTime {...props}></DatePicker>},{key:'subCate'}]],
        beforeQuery:queryParam=>{
          queryParam = queryParam || {};
          this.queryParam=queryParam;
          return queryParam;
        },
        size:'small'
      },
      fields:[
        {
          key:'id',
          rowKey:true,
          notInTable:true,
          label:'id'
        },{
          key:'billTime',
          width:'10%',
          render:(v,r)=>util.formatDate(v),
          label:''
        },{
          key:'io',
          width:'10%',
          enums:['',''],
          label:''
        },{
          key:'amount',
          width:'10%',
          label:'()'
        },{
          key:'domain',
          width:'10%',
          enums:['','','',''],
          label:''
        },{
          key:'cate',
          width:'10%',
          label:''
        },{
          key:'subCate',
          width:'10%',
          label:''
        },{
          key:'reason',
          width:'10%',
          label:''
        },{
          key:'status',
          width:'10%',
          enums:[{key:1,text:''},{key:0,text:''}],
          label:''
        },{
          key:'operator',
          width:'10%',
          label:''
        }
      ],
      // operationAddon:record=>{
      //   return (<span><a onClick={()=>{
          
      //   }}></a></span>);
      // },
      form:{
        col:1,
        beforeAdd:formData=>{
          return {status:'0'};
        },
        beforeModify:formData=>{
          return formData;
        },
        beforeSubmit:this.getFormData
      },
      table:{
        bordered:true,
        lazyInit:true,
        size:'small',
        resultHandler:(result,queryParam)=>{
          result.list=result.data.data;
          result.totalItem=result.data.totalItem;
        }
      },
      api:{
        queryInterface:{func:urlPrefix+'BillOperation?opt=queryBill'},
      }
    };
    this.state={
    };
  }

  render() {
    return (
      <div>
       <BizComp config={this.config}/>
      </div>
    );
  }
}